import React, { useState, useEffect } from "react";
import styles from "./styles/Messages.module.css";
import { useSelector } from "react-redux";
import { BsSearch } from "react-icons/bs";
import { chats as chatData } from "../common/chatData";
import moment from "moment";

function Messages(props) {
  const user = useSelector((state) => state.user);
  const [chats, setChat] = useState([]);

  useEffect(() => {
    setChat(chatData);
  }, []);

  const RenderName = ({ users }) =>
    users.map((otherUser) => {
      if (otherUser._id !== user._id) {
        return <h3 className={styles.name}>{otherUser.name}</h3>;
      }
    });

  // const RenderDate = (date) => {
  //   const dt = new Date();
  //   // console.log(moment(date).month());
  //   console.log(moment(date).fromNow());
  //   if (
  //     moment(date).date() === moment(dt).date() &&
  //     moment(date).month() === moment(dt).month() &&
  //     moment(date).year() === moment(dt).year()
  //   )
  //     return moment(date).format("LT");
  //   else return moment(date).date("L");
  // };

  const handleClick = (chat) => {
    let data = null;
    chat.users.map((otherUser) => {
      if (otherUser._id !== user._id) {
        data = otherUser;
      }
    });
    props.handleMessage(data);
  };

  const renderChats = chats.map((chat, index) => {
    return (
      <div className={styles.chatContainer}>
        <div
          className={styles.chatBox}
          key={index}
          onClick={() => handleClick(chat)}
        >
          <img
            src={
              "data:image/jpeg;base64,"
            }
            className={styles.image}
          />
          <div className={styles.content}>
            <div className={styles.date}>{moment().format("DD-MM-YYYY")}</div>
            <RenderName users={chat.users} />
            <div className={styles.text}>{chat.messages[0].content.text}</div>
          </div>
        </div>
      </div>
    );
  });

  return (
    <div className={styles.mainContainer}>
      <div className={styles.searchContainer}>
        <div className={styles.searchBox}>
          <BsSearch style={{ color: "#ced8e4" }} />
          <input type={"text"} placeholder="Search..." />
        </div>
      </div>
      {renderChats}
    </div>
  );
}

export default Messages;
